import CoverageMap from './CoverageMap';
import CoverageClient from './CoverageClient';
import { coverageData } from './coverageData';

export default function CoveragePage() {
    return (
        <section className="mt-8 space-y-8">
            <h1 className="mb-4 text-3xl font-bold text-cyan-400">Coverage Areas</h1>
            <p className="text-gray-300">
                Approximate coverage zones are shown below. Click on a shape for details!
            </p>
            <div className="glass-panel p-4">
                <CoverageMap coverageItems={coverageData} />
            </div>

            {/* Coverage list */}
            <CoverageClient coverageList={coverageData} />
        </section>
    );
}
